* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    max-width: 1230px;
    padding: 0 15px;
    margin: auto;
}

.for-bg {
    background: #000;
}
.block {
    padding-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    row-gap: 20px;
    flex-wrap: wrap;
}
.card {
    max-width: 350px;
    border: 1px solid #fff;
    border-radius: 20px;
    padding: 15px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
        color: #fff;
        text-transform: capitalize;
}
.img-box {
    max-width: 310px;

}
.img-box img {
    width: 100%;
    
}
button {
    padding: 7px 10px;
    border: none;
    border-radius: 10px;
    color: #fff;
    text-transform: capitalize;
    background: blue;
    cursor:pointer;
}
input{
    width: 30%;color: #000;
    cursor: pointer;
}
.input-box{
    width: 100%;display: flex;justify-content: center;
    padding: 20px 0;

}